.header {
    height: 60px;
    margin-bottom: 21px;
}

.header_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header_logo {
    width: 100px;
    height: auto;
}

.header_main_page.menu_logo_disabled {
    cursor: auto;
}

.nav_wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
}

.nav {
    margin: 0 auto;
    display: flex;
    align-items: center;
}

.nav_list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
}

.nav_item_link {
    position: relative;
    display: block;
    height: 100%;
    @include action-link-button;
}

.header_btn_wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.header_btn {
    position: relative;
    display: flex;
    align-items: flex-start;
    gap: 7px;
    // height: 24px;
    @include action-link-button;

    &_logo {
        width: 20px;
        height: 20px;
    }
}

.hover_border {
    &::after {
        position: absolute;
        top: 100%;
        left: 50%;
        z-index: 2;
        display: block;
        content: "";
        width: 0;
        height: 2px;
        background-color: $text-dark;
        transition: all 0.5s linear;

        transform: translateX(-50%);
        opacity: 0;
    }

    &:hover::after {
        width: 100%;
        opacity: 1;
    }
}

.menu_active {
    cursor: default;
}

.menu_active::after {
    position: absolute;
    top: 100%;
    left: 50%;
    z-index: 2;
    display: block;
    content: "";
    width: 100%;
    height: 2px;
    background-color: $text-dark;
    transition: all 0.5s linear;

    transform: translateX(-50%);
    opacity: 1;
}

.burger_btn {
    display: none;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
    border-radius: 100px;
    border: 1px solid $border-dark;
}

.burger_btn_icon {
    position: relative;

    &::after {
        top: 3px;
    }

    &::before {
        top: -5px;
    }
}

.burger_btn_icon::after,
.burger_btn_icon::before {
    position: absolute;
    z-index: 2;
    transform: translateX(-50%);
    width: 16px;
    height: 2px;
    content: "";
    background-color: $border-dark;
}

@media screen and (max-width: 768px) {
    .nav_wrapper {
        display: none;
    }

    .burger_btn {
        display: flex;
    }
}